.container {
    max-width: 600px;
    border-radius: 0.5rem;
    text-align: center;
    margin: 2rem auto;
    padding: 1rem;
    background: white;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border: 1px solid grey;

    div {
        margin: 1rem 0;
        display: flex;
        justify-content: center;
    }
    label {
        margin-right: 1rem;
    }
}

.selectInputsContainer {
    display: flex;
}